export default {
	data() {
		return {
			orderInfo: {}
		}
	},
	onLoad(params) {
		this.init(params.orderId);
	},
	methods: {
		// 支付
		pay() {
			let _this = this;
			_this.request({
				url: _this.http.pay,
				data: {
					orderId: _this.orderInfo.orderId,
					openId: uni.getStorageSync("userInfo").openId
				},
				success: (res) => {
					if (res.code === '0') {
						if (res.result.result_code.toLowerCase() === "success" && res.result.return_code.toLowerCase() === "success") {
							uni.requestPayment({
								provider: 'wxpay',
								timeStamp: res.result.timeStamp,
								nonceStr: res.result.nonce_str,
								package: `prepay_id=${res.result.prepay_id}`,
								signType: 'MD5',
								paySign: res.result.paySign,
								success: function(res) {
									uni.showToast({
										title: "支付成功",
										icon: 'success'
									});
									setTimeout(()=>{
										_this.init(_this.orderInfo.orderId);
									}, 1500);
								},
								fail: function(err) {
									uni.showToast({
										title: "支付失败",
										icon: 'none'
									});
								}
							});
						} else {
							uni.showToast({
								title: "支付失败",
								icon: 'none'
							});
						}
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		},
		callPhone() {
			uni.makePhoneCall({
				phoneNumber: '18302846993'
			});
		},
		callDriver() {
			uni.makePhoneCall({
				phoneNumber: this.orderInfo.driverMobile
			});
		},
		cancelOrder() {
			let _this = this;
			uni.showModal({
				title: '提示',
				content: '是否取消该订单',
				success: function(res) {
					if (res.confirm) {
						_this.request({
							url: _this.http.orderCancel,
							data: {
								orderId: _this.orderInfo.orderId
							},
							success: (res) => {
								if (res.code === '0') {
									uni.showToast({
										title: "取消成功",
										icon: 'success'
									});
									_this.init(_this.orderInfo.orderId);
								} else {
									uni.showToast({
										title: res.message,
										icon: 'none'
									});
								}
							}
						});
					}
				}
			});
		},
		init(orderId) {
			let _this = this;
			_this.request({
				url: _this.http.orderDetail,
				data: {
					orderId: orderId
				},
				success: (res) => {
					if (res.code === '0') {
						_this.orderInfo = res.result;
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		}
	}
}
